import { AccountInformationComponent } from './account-information/account-information.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common'; 
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { ConfigPrintComponent } from './config-print/config-print.component';
import { SearchModule } from '../components/search/search.module';
import { ComponentsModule } from './components/components.module';   
import { NgZorroAntModule } from '../desktop/ng-zorro-ant.module';


const routes: Routes = [
  {
    path: 'sales-order',
    loadChildren: () => import('./sales-order/sales-order.module').then(m => m.SalesOrderModule)
  }, 
  {
    path: 'sales-order-voucher',
    loadChildren: () => import('./sales-order-voucher/sales-order-voucher.module').then(m => m.SalesOrderVoucherModule)
  },
  {
    path: 'sales-order-payment-card',
    loadChildren: () => import('./sales-order-payment-card/sales-order-payment-card.module').then(m => m.SalesOrderPaymentCardModule),
  },
  {
    path: 'exchange-order-voucher',
    loadChildren: () => import('./exchange-order-voucher/exchange-order-voucher.module').then(m => m.ExchangeOrderVoucherModule),
  },
  {
    path: 'exchange-order-payment-card',
    loadChildren: () => import('./exchange-order-payment-card/exchange-order-payment-card.module').then(m => m.ExchangeOrderPaymentCardModule),
  }
]
@NgModule({
  declarations: [
    ConfigPrintComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    ComponentsModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    IonicModule,
    SearchModule, 
  ] 
})
export class BillWebAppModule { }
