import { VhAuth } from 'ionic-vhframeworks';
import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-change-price-type',
  templateUrl: './change-price-type.component.html',
  styleUrls: ['./change-price-type.component.scss']
})
export class ChangePriceTypeComponent implements OnInit {
  @Input() showModalChangePriceType:boolean = false;
  @Output() changePriceType : EventEmitter<any> = new EventEmitter()
  @Input() default_price_type:any;
  constructor(
    public vhAuth:VhAuth
  ) { }
  

  ngOnInit(): void {
  }
 

  handleOk(){
    this.changePriceType.emit(this.default_price_type)
  }

  handleCancel(){
    this.changePriceType.emit(null)
  }
}
